/**
 * Take a timestamp in the format HH:MM:SS or MM:SS and
 * convert it to seconds
 */
export default function timeStampToSeconds(timestamp){
    return timestamp.split(":")
    .reverse()
    .map( x => Number(x))
    .reduce( (acc, val, idx)=>{
        return (60 ** (idx)) * val + acc;
    }, 0)
}